.contImg {
  width: 250px;
  height: 150px;
  float: right;
  transition: all 1000ms ease-in-out;
}
.contImg:hover {
  border: none;
}
.img {
  width: 200px;
  height: 100px;
  display: flex;
  margin: auto;
  padding: 20px;
  transition: all 200ms ease-in-out;
}
.img:hover {
  transform: scale(1.2);
}
.j h1 {
  text-align: center;
}
.j p {
  padding: 50px;
}
