.app-wrapper{
  display: grid;
  margin: 0 auto;
  width: 1200px;
  grid-template-rows: 60px 1fr 1fr;
  grid-template-columns: 2fr 10fr;
  gap: 10px;
  grid-template-areas: 
  "h h"
  "n c"
  "f f"
  ;
}
.app-wrapper-content{
    grid-area: c;
    background-color: rgb(168, 221, 225);
}

